/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Typography, Select, Descriptions, Tag } from 'antd';
import React, { useEffect, useState, useRef } from 'react';
import jobService from '../../services/job';
import UserService from '../../services/user';
import moment from "moment"
import Pagination from '../components/Pagination';

export default function JobList({ userId = null }) {

    const [data, setData] = useState([]);
    const [user, setUser] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, userId });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: 'Receiver Name',
            dataIndex: 'receiverName',
            width: 300,
        },
        {
            title: 'User Name',
            dataIndex: 'userDetails',
            width: 200,
            render: v => v?.userName
        },
        {
            title: 'Phone',
            dataIndex: 'phone',
            width: 100,
        },
        {
            title: 'Service',
            dataIndex: 'serviceDetail',
            width: 200,
            render: (v) => v?.name
        },
        {
            title: 'Date Choosen',
            dataIndex: 'selectedDate',
            render: (sd) => sd?.map(v => <Tag key={v}>{moment(v).format('DD MMM YYYY')}</Tag>)
        },
        {
            title: 'Time Choosen',
            dataIndex: 'selectedTime',
            render: (sd) => sd?.map((v, i) => <Tag key={v._id}>{moment(v.startTime).format('h:mm a')} - {moment(v.endTime).format('h:mm a')}</Tag>)
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 150,
            render: v => <Tag color='blue'>{v}</Tag>
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        jobService.listJob(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    function userlist() {
        setLoading(true);
        UserService.listUser(qData).then(res => {
            setUser(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, qData, setQData, list, userId, users: user }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);


    useEffect(() => {
        userlist();
    }, [])

    return (
        <>
            {
                userId
                    ? null
                    : <Typography.Title level={5} style={{ marginTop: 0 }}>List of Jobs</Typography.Title>
            }

            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
                expandable={{
                    expandedRowRender: (record) => (
                        <Descriptions title="Job Info">
                            <Descriptions.Item label="WorkerName">{record?.workerDetail ? record?.workerDetail.userName : 'Not Accepted'}</Descriptions.Item>
                            <Descriptions.Item label="Worker Phone">{record?.workerDetail ? record?.workerDetail.phone : 'Empty'}</Descriptions.Item>
                            <Descriptions.Item label="Live">{record.addressDetail.state}</Descriptions.Item>
                            <Descriptions.Item label="Remark">empty</Descriptions.Item>
                            <Descriptions.Item label="Address">
                                {record.addressDetail.street1} ,{record.addressDetail.street2} ,{record.addressDetail.city}
                            </Descriptions.Item>
                        </Descriptions>
                    ),
                    rowExpandable: (record) => record.receiverName !== 'Not Expandable',
                }}
            />
        </>
    );
};

function Search({ qData, setQData, userId, list, users }) {

    const [userNames, setUserNames] = useState([]);

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }
    }

    useEffect(() => {
        const Names = users?.map((item) => (
            {
                label: item.userName ? <><p style={{ padding: "0px", margin: "0px" }}>{item.userName}</p><p style={{ fontSize: "12px", margin: "0px" }}><Typography.Text type="danger">@{item.phone}</Typography.Text></p></> : item.phone,
                value: item.userName ? item.userName : item.phone,
                searchKey: item.userName + item.phone
            }
        ))
        setUserNames(Names)
    }, [users])

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                {
                    !userId &&
                    <>
                        <Col span={4}>
                            <Form.Item style={{ marginBottom: 0 }}>
                                <Select
                                    showSearch
                                    allowClear
                                    placeholder="Select by username / phone"
                                    optionFilterProp="children"
                                    onChange={(e) => onChange(e, "key")}
                                    filterOption={(input, option) =>
                                        (option?.searchKey ?? '').toLowerCase().includes(input.toLowerCase())
                                    }
                                    options={userNames}
                                />
                            </Form.Item>
                        </Col>
                        {/* <Col span={8}>
                            <Form.Item style={{ marginBottom: 0 }}>
                                <DatePicker.RangePicker
                                    format="DD MMM YYYY"
                                    style={{ width: '100%' }}
                                    value={[qData?.startDate, qData?.endDate]}
                                    onChange={v => (setQData({ ...qData, startDate: v?.[0], endDate: v?.[1] }))}
                                    allowClear={true}
                                />
                            </Form.Item>
                        </Col> */}
                        <Col span={3}>
                            <Form.Item style={{ marginBottom: 0 }}>
                                <Button type="primary" htmlType="submit">Search</Button>
                            </Form.Item>
                        </Col>
                    </>
                }
                <Col span={5}>

                </Col>
            </Row>
        </Form>
    );
};