import { DashboardOutlined, BankOutlined, ApartmentOutlined, CalendarOutlined } from '@ant-design/icons';

import Dashboard from '../pages/Dashboard';
import Setting from "../pages/Setting"
import Service from '../pages/Service';
import WorkerManagement from '../pages/management/workerManagement';
import UserManagement from '../pages/management/userManagement';
import JobList from '../pages/JobList';
import WorkHistory from '../pages/WorkHistory';
import Leave from '../pages/Leave';
import Profile from '../pages/admin/Profile';
import ChangePassword from '../pages/admin/Change-password';


const routes = [
    { title: 'Dashboard', icon: DashboardOutlined, url: '/', component: Dashboard },
    { title: 'Service', icon: ApartmentOutlined, url: '/service', component: Service },
    { title: 'Job List', icon: ApartmentOutlined, url: '/job-list', component: JobList },
    {
        title: 'Management', icon: BankOutlined, url: '/management', children: [
            { title: 'User', icon: ApartmentOutlined, url: '/', component: UserManagement },
            { title: 'Worker', icon: ApartmentOutlined, url: '/Worker', component: WorkerManagement },
        ]
    },
    { title: 'Working History', icon: CalendarOutlined, url: '/work-history', component: WorkHistory },
    { title: 'Leave Requests', icon: CalendarOutlined, url: '/leave', component: Leave },
    { title: 'Settings', icon: CalendarOutlined, url: '/Setting', component: Setting },
    {
        title: 'You', icon: ApartmentOutlined, url: '/profile', children: [
            { title: 'Profile', icon: ApartmentOutlined, url: '/', component: Profile },
            { title: 'Change Password', icon: ApartmentOutlined, url: '/change-password', component:ChangePassword },
        ]
    },
].filter(v => (!v.hidden) || v.children?.length);

export default routes;