/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Input, Tag } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined, PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import WorkerService from '../../../services/worker';
import Service from '../../../services/service';
import UploadImage from '../../../utils/UploadImage';
import Pagination from '../../components/Pagination';
import { AntdMsg } from '../../../utils/Antd';
import WorkHistory from '../WorkHistory';
import accountService from '../../../services/account';


export default function WorkerManagement() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, });
    const addNewModalRef = useRef();
    const ModelModalRef = useRef();
    const accountModalRef = useRef();
    const [service, setService] = useState([]);
    const columns = [
        {
            title: 'Username',
            dataIndex: 'userName',
        },
        {
            title: 'Phone',
            dataIndex: 'phone',
            width: 100,
        },
        {
            title: 'Service',
            dataIndex: 'serviceDetail',
            width: 250,
            render: (service) => service?.map(v => <Tag key={v._id}>{v.name}</Tag>)
        },
        {
            title: 'WorkList',
            dataIndex: '_id',
            width: 80,
            render: (v, row) => {
                return <>
                    <Button type="primary" size='small' onClick={() => { ModelModalRef.current.openForm({ _id: row._id, userName: row.userName }) }} >Works</Button>
                </>
            }
        },
        {
            title: 'Account',
            dataIndex: '_id',
            width: 80,
            render: (v, row) => {
                return <>
                    <Button type="primary" size='small' onClick={() => { accountModalRef.current.openForm({ _id: row._id, userName: row.userName }) }} >Account</Button>
                </>
            }
        },
        {
            title: 'Rate Per Hour',
            dataIndex: 'per_hr_rate',
            width: 120,
            render: (v) => <b> ₹ {v} </b>
        },
        {
            title: 'Status',
            dataIndex: 'status',
            width: 80,
            render: isActive => {
                if (isActive) {
                    return <Tag color='green'>Active</Tag>
                } else {
                    return <Tag color='red'>Inactive</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this Worker?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        setLoading(true);
        WorkerService.listWorker(qData).then(res => {
            setData(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }
    function listService() {
        Service.listService()
            .then(res => { setService(res.data || []); }).catch(err => { AntdMsg('Service data not loaded', 'error'); });
    }

    const deleteData = (id) => {
        WorkerService.deleteWorker(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
        listService();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Workers</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list, service }} />
            <ModelModal ref={ModelModalRef} />
            <AccountModal ref={accountModalRef} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, }) {

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }
    }

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col>
                <Col span={6}>
                    <Select
                        placeholder="Please select..."
                        allowClear
                        onChange={e => onChange(e, "status")}
                        style={{
                            width: '100%',
                        }}
                        options={
                            [
                                {
                                    value: true,
                                    label: "Registerd",
                                },
                                {
                                    value: false,
                                    label: "Not Registerd",
                                }
                            ]
                        }
                    />
                </Col>
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list, service } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);
    const [aadharImg, setAadharImg] = useState([]);
    const [nocImg, setNocImg] = useState([]);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { status: true }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        WorkerService.detailWorker(data._id).then(res => {
            const presentAddress = res.data?.addressDetail?.find(v => v.workerAddressType === "present");
            const parmanentAddress = res.data?.addressDetail?.find(v => v.workerAddressType === "parmanent");

            setData({
                ...res.data,
                presentAddress: presentAddress && `${presentAddress?.street1}, ${presentAddress?.street2}, ${presentAddress?.city}, ${presentAddress?.state} (${presentAddress?.pin})`,
                parmanentAddress: parmanentAddress && `${parmanentAddress?.street1}, ${parmanentAddress?.street2}, ${parmanentAddress?.city}, ${parmanentAddress?.state} (${parmanentAddress?.pin})`,
            } || {});
            setFileList(res.data?.avatar ? [res.data?.avatar] : []);
            setAadharImg(res.data?.aadharImage ? [res.data?.aadharImage] : []);
            setNocImg(res.data?.nocImage ? [res.data?.nocImage] : []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        WorkerService.saveWorker(data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false)
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        } else {
            setFileList([]);
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setFileList([]);
            setAadharImg([]);
            setNocImg([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ avatar: fileList?.[0] });
        handleChange({ aadharImage: aadharImg?.[0] });
        handleChange({ nocImage: nocImg?.[0] });
    }, [fileList, aadharImg, nocImg]);

    return (
        <>

            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Worker'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={800}
                className="app-modal-body"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={20}>
                                <Row gutter={[12, 2]}>
                                    <Col span={12}>
                                        <Form.Item label="Username" required>
                                            <Input placeholder="John Doe" value={data.userName} onChange={e => { handleChange({ userName: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>

                                    <Col span={12}>
                                        <Form.Item label="Phone" required>
                                            <Input placeholder="Phone" value={data.phone} onChange={e => { handleChange({ phone: e.target.value }) }} />
                                        </Form.Item>
                                    </Col>
                                    <Col span={24}>
                                        <Form.Item label="Service" required>
                                            <Select
                                                mode="multiple"
                                                value={data.serviceIds}
                                                placeholder="Please select..."
                                                onChange={e => { handleChange({ serviceIds: e }) }}
                                                style={{
                                                    width: '100%',
                                                }}
                                                options={
                                                    service.map(v => (
                                                        {
                                                            value: v._id,
                                                            label: v.name,
                                                        }
                                                    ))
                                                }
                                            />
                                        </Form.Item>
                                    </Col>

                                </Row>
                            </Col>
                            <Col span={4}>
                                <Form.Item label="Avatar">
                                    <UploadImage {...{ fileList, setFileList }} count={1} />
                                </Form.Item>
                            </Col>

                            <Col span={24}>
                                <Form.Item label="Present Address">
                                    <Input.TextArea placeholder="Present Address" value={data.presentAddress} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={24}>
                                <Form.Item label="Permanent Address">
                                    <Input.TextArea placeholder="Permanent Address" value={data.parmanentAddress} disabled />
                                </Form.Item>
                            </Col>
                            <Col span={6}>
                                <Form.Item label="Aadhar Image">
                                    <UploadImage {...{ fileList: aadharImg, setFileList: setAadharImg }} count={1} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="NOC Image">
                                    <UploadImage {...{ fileList: nocImg, setFileList: setNocImg }} count={1} />
                                </Form.Item>
                            </Col>
                            <Col span={10}>
                                <Form.Item label="Status" required>
                                    <Select value={data.status} onChange={e => { handleChange({ status: e }) }} >
                                        <Select.Option value={true}>Active</Select.Option>
                                        <Select.Option value={false}>Inactive</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Per Hour Rate" required>
                                    <Input placeholder="Per Hour Rate" value={data.per_hr_rate} onChange={e => { handleChange({ per_hr_rate: e.target.value }) }} />
                                </Form.Item>
                            </Col>

                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});

const ModelModal = forwardRef((props, ref) => {



    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(dt ? { ...dt } : { status: true });
        }
    }));

    return (
        <>
            <Modal
                title={<>All Works of <Typography.Text type="danger">{data?.userName}</Typography.Text></>}
                style={{ top: 20 }}
                open={open}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={1600}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                ]}
            >
                <WorkHistory {...{ workerId: data._id }} />
            </Modal>
        </>
    );
});
const AccountModal = forwardRef((props, ref) => {



    const [open, setOpen] = useState(false);
    const [data, setData] = useState({});
    const [modalData, setModalData] = useState({});
    const [ajxRequesting, setAjxRequesting] = useState(false);

    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setModalData(dt ? { ...dt } : { status: true });
        }
    }));

    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }

    const getDetails = () => {
        setAjxRequesting(true);
        accountService.detail(modalData._id).then(res => {
            setData(res.data || {});
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        accountService.save({ ...data, workerId: modalData._id }).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            setOpen(false)
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (modalData?._id) {
            getDetails();
        }
    }, [modalData._id]);


    return (
        <>
            <Modal
                title={<>Bank Account Details of:  <Typography.Text type="danger">{modalData?.userName}</Typography.Text></>}
                style={{ top: 20 }}
                open={open}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={700}
                className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>,

                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 0]}>
                            <Col span={24}>
                                <Form.Item label="Bank Name" required>
                                    <Input placeholder="Bank Of India" value={data.bankName} onChange={e => { handleChange({ bankName: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Holder Name">
                                    <Input placeholder="John Doe" value={data.holderName} onChange={e => { handleChange({ holderName: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Account Number">
                                    <Input placeholder="BOI1100XXX0564X" value={data.accountNumber} onChange={e => { handleChange({ accountNumber: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="IFSC code">
                                    <Input placeholder="Permanent Address" value={data.IFSC} onChange={e => { handleChange({ IFSC: e.target.value }) }} />
                                </Form.Item>
                            </Col>
                            <Col span={12}>
                                <Form.Item label="Account Type" required>
                                    <Select value={data.accountType} onChange={e => { handleChange({ accountType: e }) }} >
                                        <Select.Option value={"saving"}>Saving</Select.Option>
                                        <Select.Option value={"current"}>Current</Select.Option>
                                    </Select>
                                </Form.Item>
                            </Col>
                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});