/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Typography } from 'antd';
import React, { useEffect, useState, useRef } from 'react';
import WorkHistoryService from '../../services/workhistory';
import Pagination from '../components/Pagination';
import moment from "moment"


const viewAccess = true;

export default function WorkHistory({ workerId = null }) {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, workerId });
    const addNewModalRef = useRef();
    const columns = [

        {
            title: 'Costumer Name',
            dataIndex: 'userServiceDetail',
            render: (v) => v?.receiverName
        },
        {
            title: 'Phone',
            dataIndex: 'userServiceDetail',
            width: 100,
            render: (v) => v?.phone
        },
        {
            title: 'Login time',
            dataIndex: 'login_time',
            width: 170,
            render: (v) => <span>{moment(v).format('DD MMM YYYY, h:mm a')}</span>
        },
        {
            title: 'Logout time',
            dataIndex: 'logout_time',
            width: 170,
            render: (v) => <span>{moment(v).format('DD MMM YYYY, h:mm a')}</span>
        },
        {
            title: 'Duration',
            dataIndex: 'duration',
            width: 100,
            render: (v) => <b>{v} min</b>
        },
        // {
        //     title: 'Status',
        //     dataIndex: 'userServiceDetail',
        //     width: 100,
        //     render: (v) => <Tag color='blue'>{v?.status}</Tag>
        // },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            WorkHistoryService.listWorkHistory(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, qData, setQData, list }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Worker History</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
        </>
    );
};

function Search({ qData, setQData, list }) {

    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                {/* <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Input placeholder="Search by name or code" value={qData.key} onChange={e => (setQData({ ...qData, key: e.target.value }))} allowClear />
                    </Form.Item>
                </Col> */}
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Refresh</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    {/* <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    } */}

                </Col>
            </Row>
        </Form>
    );
};
