/* eslint-disable react-hooks/exhaustive-deps */
import { Table, Button, Form, Row, Col, Popconfirm, Modal, Spin, Typography, Select, Image,  Tag,  Radio, Divider, DatePicker } from 'antd';
import { DeleteOutlined, EditOutlined, LoadingOutlined,PlusOutlined } from '@ant-design/icons';
import React, { useEffect, useState, forwardRef, useImperativeHandle, useRef } from 'react';
import Services from '../../services/leave';
import workerService from '../../services/worker';
import moment from "moment"
import Pagination from '../components/Pagination';
import { AntdMsg } from '../../utils/Antd';

const viewAccess = true;

export default function Leave() {

    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selected, setSelected] = useState([]);
    const [worker, setWorker] = useState([]);
    const [qData, setQData] = useState({ page: 1, limit: 20, fileType: 'photo' });
    const addNewModalRef = useRef();
    const columns = [
        {
            title: 'Worker Name',
            dataIndex: 'workerDetail',
        },
        {
            title: 'Reason',
            dataIndex: 'reason',
            width: 250,
        },
        {
            title: 'Start Date',
            dataIndex: 'startDate',
            width: 150,
            render: (v) => <span>{moment(v).format('DD MMM YYYY')}</span> 
        },
        {
            title: 'End Date',
            dataIndex: 'endDate',
            width: 150,
            render: (v) => <span>{moment(v).format('DD MMM YYYY')}</span> 
        },
        {
            title: 'Prescription',
            dataIndex: 'precipition',
            width: 100,
            render: (v) => <Image src={v?.url} height={32} />
        },
        {
            title: 'Status',
            dataIndex: 'isApproved',
            width: 150,
            render: (_, row) => {
                if (row.status === "Approved") {
                    return <Tag color='green'>Approved</Tag>
                } else if (row.status === "Rejected") {
                    return <Tag color='red'>Rejected</Tag>
                }
                else {
                    return <Tag color='blue'>Requested</Tag>
                }
            },
        },
        {
            title: 'Action',
            dataIndex: '_id',
            width: 70,
            render: (v, row) => {
                return <>
                    <Popconfirm
                        title="Are you sure to delete this Service?"
                        onConfirm={() => { deleteData(row._id); }}
                        okText="Yes"
                        cancelText="No"
                    >
                        <Button type="primary" size='small' danger icon={<DeleteOutlined />} />&nbsp;
                    </Popconfirm>
                    <Button type="primary" size='small' icon={<EditOutlined />} onClick={() => { addNewModalRef.current.openForm(row._id) }} />
                </>
            }
        },
    ].filter(item => !item.hidden);

    function list() {
        if (viewAccess) {
            setLoading(true);
            Services.list(qData).then(res => {
                setData(res.data?.map((v) => ({ ...v, key: v._id })));
                setQData({ ...qData, limit: res.extra.limit, page: res.extra.page, total: res.extra.total });
            }).catch(err => { }).finally(() => {
                setLoading(false);
            });
        }
    }
    function workerlist() {
        setLoading(true);
        workerService.listWorker(qData).then(res => {
            setWorker(res.data?.map((v) => ({ ...v, key: v._id })));
            setQData({ ...qData, isAll: 1 });
        }).catch(err => { }).finally(() => {
            setLoading(false);
        });
    }

    const deleteData = (id) => {
        Services.deleteService(id).then(res => {
            AntdMsg(res.message);
            list();
            setSelected([]);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        })
    }

    const tableColumns = columns.map((item) => ({ ...item, ellipsis: false }));

    tableColumns[0].fixed = true;
    tableColumns[tableColumns.length - 1].fixed = 'right';

    const tableProps = {
        bordered: true,
        loading,
        size: 'small',
        title: () => <Search {...{ addNewModalRef, selected, deleteData, qData, setQData, list, workers: worker }} />,
        showHeader: true,
        footer: () => <Pagination {...{ qData, setQData }} />,
        rowSelection: {
            onChange: (selectedRowKeys) => {
                setSelected(selectedRowKeys);
            },
        },
        tableLayout: undefined,
    };

    useEffect(() => {
        list();
    }, [qData.page, qData.limit]);

    useEffect(() => {
        workerlist();
    }, [])

    return (
        <>
            <Typography.Title level={5} style={{ marginTop: 0 }}>List of Leave Requests</Typography.Title>
            <Table
                {...tableProps}
                pagination={{ position: ['none'], pageSize: qData.limit }}
                columns={tableColumns}
                dataSource={data.length ? data : []}
                scroll={{ y: 'calc(100vh - 340px)', x: 'calc(100vw - 387px)' }}
            />
            <AddForm ref={addNewModalRef} {...{ list }} />
        </>
    );
};

function Search({ addNewModalRef, selected, deleteData, qData, setQData, list, workers }) {

    const [userNames, setUserNames] = useState([]);

    const onChange = (v, key) => {
        qData[key] = v;
        if (v === undefined) {
            qData[key] = "";
            list()
        }
    }

    useEffect(() => {
        const Names = workers?.map((item) => (
            {
                label: item.userName ? <><p style={{ padding: "0px", margin: "0px" }}>{item.userName}</p><p style={{ fontSize: "12px", margin: "0px" }}><Typography.Text type="danger">@{item.phone}</Typography.Text></p></> : item.phone,
                value: item.userName ? item.userName : item.phone,
                searchKey: item.userName + item.phone
            }
        ))
        setUserNames(Names)
    }, [workers])
    return (
        <Form onSubmitCapture={list} className="search-form">
            <Row gutter={[12, 2]}>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select
                            showSearch
                            allowClear
                            placeholder="Select by username / phone"
                            optionFilterProp="children"
                            onChange={(e) => onChange(e, "userName")}
                            filterOption={(input, option) =>
                                (option?.searchKey ?? '').toLowerCase().includes(input.toLowerCase())
                            }
                            options={userNames}
                        />
                    </Form.Item>
                </Col>
                <Col span={4}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Select
                            placeholder="Select Type..."
                            onChange={(v) => onChange(v, 'status')}
                            allowClear
                            options={[
                                {
                                    value: "Approved",
                                    label: 'Approved',
                                },
                                {
                                    value: "Rejected",
                                    label: 'Rejected',
                                },
                                {
                                    value: "Requested",
                                    label: 'Requested',
                                },

                            ]}
                        />
                    </Form.Item>
                </Col>
                {/* <Col span={8}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <DatePicker.RangePicker
                            style={{ width: '100%' }}
                            onChange={v => onChange(v, 'DateKey')} allowClear
                        />
                    </Form.Item>
                </Col> */}
                <Col span={3}>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Button type="primary" htmlType="submit">Search</Button>
                    </Form.Item>
                </Col>
                <Col span={5}>
                    <Form.Item style={{ marginBottom: 0, float: 'right' }}>
                        <Button type="primary" icon={<PlusOutlined />} onClick={() => { addNewModalRef.current.openForm() }}>Add New</Button>
                    </Form.Item>
                    {
                        selected.length
                            ? <Popconfirm
                                title="Are you sure to delete these selected images?. Be sure to delete the image from server."
                                onConfirm={() => { deleteData(selected); }}
                                okText="Yes"
                                cancelText="No"
                            >
                                <Button type="primary" danger style={{ float: 'right' }} icon={<DeleteOutlined />}>Delete Selected</Button>
                            </Popconfirm>
                            : null
                    }

                </Col>
            </Row>
        </Form>
    );
};

const AddForm = forwardRef((props, ref) => {
    const { list } = props;
    const [open, setOpen] = useState(false);
    const [data, setData] = useState({ aspect: 1 });
    const [ajxRequesting, setAjxRequesting] = useState(false);
    const [fileList, setFileList] = useState([]);


    const handleChange = (value) => {

        Object.entries(value).forEach(ent => {
            let varDt = data;
            const k = ent[0];
            const v = ent[1];
            let keys = k.split('.');
            for (let i = 0; i < keys.length; i++) {
                if (i + 1 === keys.length) {
                    varDt[keys[i]] = v;
                } else {
                    if (typeof varDt[keys[i]] === 'undefined') {
                        if (parseInt(keys[i + 1]) * 1 >= 0) {
                            varDt[keys[i]] = [];
                        } else {
                            varDt[keys[i]] = {};
                        }
                    } varDt = varDt[keys[i]];
                }
            }
        });
        setData({ ...data });

    }


    useImperativeHandle(ref, () => ({
        openForm(dt) {
            setOpen(true);
            setData(
                dt
                    ? { _id: dt }
                    : { fileType: 'photo' }
            );
        }
    }));

    const getDetails = () => {
        setAjxRequesting(true);
        Services.detail(data._id).then(res => {
            setData(res.data || {});
            setFileList(res.data?.file ? [res.data?.file] : []);
        }).catch(err => {
            AntdMsg(err.message, 'error');
        }).finally(() => {
            setAjxRequesting(false);
        });
    }

    const save = () => {
        setAjxRequesting(true);
        Services.approve(data._id, data).then((res) => {
            AntdMsg(res.message);
            handleChange({ ...res.data });
            list();
            setOpen(false);
        }).catch(err => {
            if (typeof err.message === 'object') {
                let dt = err.message[Object.keys(err.message)[0]];
                AntdMsg(dt, 'error');
            } else {
                AntdMsg(err.message, 'error');
            }
        }).finally(() => {
            setAjxRequesting(false);
        });

    }

    useEffect(() => {
        if (data._id) {
            getDetails();
        }
    }, [data._id]);

    useEffect(() => {
        if (!open) {
            setData({ _id: null });
        } else {
            setFileList([]);
        }
    }, [open]);

    useEffect(() => {
        handleChange({ file: fileList?.[0] });
    }, [fileList]);

    return (
        <>
            <Modal
                title={(!data._id ? 'Add' : 'Edit') + ' Leave'}
                style={{ top: 20 }}
                open={open}
                okText="Save"
                onOk={save}
                okButtonProps={{ disabled: ajxRequesting }}
                onCancel={() => { setOpen(false); }}
                destroyOnClose
                maskClosable={false}
                width={500}
                // className="app-modal-body-overflow"
                footer={[
                    <Button key="cancel" onClick={() => { setOpen(false); }}>Cancel</Button>,
                    <Button key="save" type="primary" onClick={save}>Save</Button>
                ]}
            >
                <Spin spinning={ajxRequesting} indicator={<LoadingOutlined style={{ fontSize: 24 }} spin />}>
                    <Form layout="vertical">
                        <Row gutter={[12, 2]}>
                            <Col span={6}>
                                <Divider orientation="left">Leave Application Status</Divider>
                                <Radio.Group onChange={(e) => handleChange({ status: e.target.value })} value={data.status} style={{ display: "flex", gap: "20px", alignItems: "center" }} >
                                    <Radio value="Approved">Approved</Radio>
                                    <Radio value="Requested">Requested</Radio>
                                    <Radio value="Rejected">Rejected</Radio>
                                </Radio.Group>

                            </Col>
                            <Col span={24}>
                                <Divider orientation="left">Leave Date</Divider>
                                <DatePicker.RangePicker
                                    style={{ width: '100%' }}
                                    value={[moment(data.startDate), moment(data.endDate)]}
                                    onChange={v => handleChange({ startDate: new Date(v[0]), endDate: new Date(v[1]) })} allowClear
                                />

                            </Col>

                        </Row>
                    </Form>
                </Spin>
            </Modal>
        </>
    );
});